.btn {
  background-color: #131c3f;
  color: #ebffeb;
  padding: 0.7em 1.2em;
  text-decoration: none;
  transition: 0.3s;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  box-shadow: 2px 2px 2px 3px rgba(0, 0, 0, 0.2);
}

.btn:hover {
  color: #fe5d26;
  padding: .8em 1.3em;
}
